  ngAfterViewInit() {
    const preBtn = <HTMLElement>document.getElementById('preBtn');
    const moveTab = <HTMLElement>document.querySelector('.move-tab');
    preBtn.style.visibility = 'hidden';
    // to ensure moveTab can stay correct position
    $(window).resize(() => {
      const screenWidth = document.body.clientWidth;
      if (screenWidth > 990) {
        moveTab.style.width = 'calc((100% - 260px)/2)';
        if (this.tabIndex === 1) {
          moveTab.style.left = '20vw';
        } else if (this.tabIndex === 2) {
          moveTab.style.left = '42vw';
        }
      } else {
        moveTab.style.width = '34%';
        if (this.tabIndex === 1) {
          moveTab.style.left = '30vw';
        } else if (this.tabIndex === 2) {
          moveTab.style.left = '61vw';
        }
      }
    });
    const tabs = document.getElementsByClassName('wizard-tab');
    for (let i = 1; i < tabs.length; i++) {
      (<HTMLElement>tabs[i]).style.display = 'none';
    }
  }

  preOnClick() {
    const moveTab = <HTMLElement>document.querySelector('.move-tab');
    const nextBtn = <HTMLElement>document.getElementById('nextBtn');
    const preBtn = <HTMLElement>document.getElementById('preBtn');
    const tabs = document.getElementsByClassName('wizard-tab');
    const screenWidth = document.body.clientWidth;
    for (let i = 0; i < tabs.length; i++) {
      (<HTMLElement>tabs[i]).style.display = 'none';
    }
    if (this.tabIndex === 1) {
      this.tabIndex--;
      moveTab.style.left = '-1vw';
      preBtn.style.visibility = 'hidden';
      nextBtn.style.visibility = 'visible';

      moveTab.innerHTML = 'Connection';
    } else if (this.tabIndex === 2) {
      this.tabIndex--;
      moveTab.style.left = screenWidth > 990 ? '20vw' : '30vw';
      preBtn.style.visibility = 'visible';
      nextBtn.style.visibility = 'hidden';

      moveTab.innerHTML = 'Query';
    }
    (<HTMLElement>tabs[this.tabIndex]).style.display = 'inherit';
  }

  nextOnClick() {
    const moveTab = <HTMLElement>document.querySelector('.move-tab');
    const nextBtn = <HTMLElement>document.getElementById('nextBtn');
    const preBtn = <HTMLElement>document.getElementById('preBtn');
    const tabs = document.getElementsByClassName('wizard-tab');
    const screenWidth = document.body.clientWidth;
    for (let i = 0; i < tabs.length; i++) {
      (<HTMLElement>tabs[i]).style.display = 'none';
    }
    if (this.tabIndex === 0) {
      this.waiting = true;
      this.registerdriver();
      this.getConnection();
      this.tabIndex++;

      moveTab.style.left = screenWidth > 990 ? '20vw' : '30vw';
      preBtn.style.visibility = 'visible';
      moveTab.innerHTML = 'Query';
      nextBtn.innerHTML = "Next";

      //nextBtn.style.visibility = 'hidden';

    } else if (this.tabIndex === 1) {
      if (this.selectedConnections == "") {
        swal({
          title: 'Error!',
          text: "Connection Invalid",
          type: 'warning',
          confirmButtonClass: 'btn btn-danger'
        });
        this.tabIndex = 1;
      } else {
        this.tabIndex++;
        moveTab.style.left = screenWidth > 990 ? '42vw' : '61vw';
        nextBtn.style.visibility = 'hidden';
        moveTab.innerHTML = 'Extraction';
      }
    }
    (<HTMLElement>tabs[this.tabIndex]).style.display = 'inherit';
  }


  tabClick(id) {
    const moveTab = <HTMLElement>document.querySelector('.move-tab');
    const nextBtn = <HTMLElement>document.getElementById('nextBtn');
    const preBtn = <HTMLElement>document.getElementById('preBtn');
    const tabs = document.getElementsByClassName('wizard-tab');
    const screenWidth = document.body.clientWidth;
    for (let i = 0; i < tabs.length; i++) {
      (<HTMLElement>tabs[i]).style.display = 'none';
    }

    this.tabIndex = parseInt(id)

    if (this.tabIndex === 0) {
      //this.waiting = true;
      this.tabIndex = 0;
      moveTab.style.left = '-1vw';
      nextBtn.style.visibility = 'visible';
      preBtn.style.visibility = 'hidden';
      nextBtn.innerHTML = "Add Connection";

      moveTab.innerHTML = 'Connection';

    } else if (this.tabIndex === 1) {
      this.getConnection();
      this.tabIndex = 1;
      moveTab.style.left = screenWidth > 990 ? '20vw' : '30vw';
      nextBtn.style.visibility = 'visible';
      preBtn.style.visibility = 'visible';
      nextBtn.innerHTML = "Next";
      moveTab.innerHTML = 'Query';
    }
    else if (this.tabIndex === 2) {
      
      /*
      
      if (this.selectedConnections == "") {
        swal({
          title: 'Error!',
          text: "Connection Invalid",
          type: 'warning',
          confirmButtonClass: 'btn btn-danger'
        });
        this.tabIndex = 1;
      } else {
        */
      this.tabIndex = 2;
      //this.getScript();
      moveTab.style.left = screenWidth > 1000 ? '42vw' : '60vw';
      nextBtn.style.visibility = 'hidden';
      preBtn.style.visibility = 'visible';

      moveTab.innerHTML = 'Extraction';
    }



    (<HTMLElement>tabs[this.tabIndex]).style.display = 'inherit';
  }

