import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './dashboard/home/home.component';
import { ProfileComponent } from './dashboard/profile/profile.component';
import { TableComponent } from './dashboard/table/table.component';
import { NotificationComponent } from './dashboard/notification/notification.component';
import { SweetAlertComponent } from './dashboard/sweetalert/sweetalert.component';
import { SettingsComponent } from './dashboard/settings/settings.component';
import { PriceTableComponent } from './dashboard/component/pricetable/pricetable.component';
import { PanelsComponent} from './dashboard/component/panels/panels.component';
import { WizardComponent } from './dashboard/component/wizard/wizard.component';

import { RootComponent } from './dashboard/root/root.component';
import { LoginComponent } from './page/login/login.component';
import { LockComponent } from './page/lock/lock.component';
import { RegisterComponent } from './page/register/register.component';
import { ConnectionComponent } from './dashboard/connection/connection.component';
import { CreateConnectionComponent } from './dashboard/create-connection/create-connection.component';
import { ExtractionComponent } from './dashboard/extraction/extraction.component';
import { CommonModule } from '@angular/common';
import{ListarErrComponent} from './listar-err/listar-err.component';
import {RegistoComponent} from './dashboard/registo/registo.component';
import {AccessManagementComponent} from './dashboard/access-management/access-management.component';

import {AuthGuard} from './guards/auth.guard';
import {AdminGuard} from './guards/admin.guard';
import {LoginGuard} from './guards/login.guard';


const routes: Routes = [
  {path: '', component: LoginComponent,canActivate:[LoginGuard]},
  {path: 'lock', component: LockComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'dashboard', component: RootComponent,canActivate:[AuthGuard],children: [
    {path: '', component: HomeComponent},
    {path: 'profile', component: ProfileComponent},
    {path: 'Create_Connection', component:CreateConnectionComponent },
    {path: 'extraction', component:ExtractionComponent },
    {path: 'table', component: TableComponent},
    {path: 'listarE/:id', component: ListarErrComponent},
    {path: 'notification', component: NotificationComponent},
    {path: 'alert', component: SweetAlertComponent},
    {path: 'settings',component: SettingsComponent,canActivate:[AdminGuard]},
    {path: 'Access-Management', component: AccessManagementComponent},
    {path: 'registo', component: RegistoComponent,canActivate:[AdminGuard]},
    {path: 'connection',component: ConnectionComponent,canActivate:[AdminGuard]},
    {path: 'components/price-table', component: PriceTableComponent},
    {path: 'components/panels', component: PanelsComponent},
    {path: 'components/wizard', component: WizardComponent}
  ]}
];

export const routing = RouterModule.forRoot(routes,{useHash:true});


