@Pipe({
  name: 'prettyprint1'
})

export class PrettyPrintPipe1 implements PipeTransform {
  public error: Array<Object> = [];
  ret: String = "";

  transform(val): any {

    this.error = val;
    for (let index = 0; index < this.error.length; index++) {
      this.ret = this.ret + " " + this.error[index] + "/n"
    }

    return this.ret;

  }
}
